import { Component, ViewChild, Input, Output, EventEmitter, ElementRef} from '@angular/core';

/**
 * Generated class for the SectionComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'ibmsection',
  templateUrl: 'section.html'
})
export class SectionComponent {
  visibility: string = 'hidden';
  @ViewChild("content") content: ElementRef;
  @Input() title: String = "titolo";
  @Input() showDelete: Boolean = false;
  @Input() showCopy: Boolean = false;
  @Input() showAdd: Boolean = false;
  @Input() showAlert: Boolean = false;
  @Input() background: String ="red";
  @Input() color: String ="yellow";
  @Input() visible: Boolean =false;
  @Output() onDeleteButton = new EventEmitter<any>();
  @Output() onAddButton = new EventEmitter<any>();
  @Output() onCopyButton = new EventEmitter<any>();
  //visible=false;
  text: string;
  headerclass="arrheader";
  headercolor="yellow";
  animationclass="";
  constructor() {
  
    this.text = 'Hello World';
    if (this.showDelete) this.headerclass="arrheader"
  }

  toggleSection(){
    console.log("togglesection")
    this.visible=!this.visible;
    //if (this.content) this.animationclass="animated cucuzza";
    
  }

  getStyle(){
    var retvalue="background: red !important";
    return retvalue;
  }


  addItem(){
    this.onAddButton.emit("add!!")
    this.visible=true;
  }

  copyItem(){
    this.onCopyButton.emit("add!!")
  }
  deleteItem(){
    this.onDeleteButton.emit("delete!!")
  }


}



