import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import { BackendProvider } from '../../providers/backend/backend';
/**
 * Generated class for the SendpushPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@Component({
  selector: 'page-sendpush',
  templateUrl: 'sendpush.html',
})
export class SendpushPage {

  titolo="";
  text="";
  constructor(private alertCtrl: AlertController, private backend: BackendProvider, public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SendpushPage');
  }


  sendPush(){
    var questo=this;
    let alert = questo.alertCtrl.create({
      title: 'Conferma invio notifica',
      message: 'Vuoi davvero inviare la notifica push ?',
      buttons: [
        {
          text: 'Annulla',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'OK',
          handler: () => {
            console.log('Buy clicked');
            var url=questo.backend.rooturl+"/fcm/send?title="+questo.titolo+"&text="+questo.text;
            questo.backend.fetchData(url,function(data){
              console.log("push sent",data);
              
              questo.backend.showAlert("Messaggio push inviato !!");
            })
          }
        }
      ]
    });
    alert.present();
  
  }
}



