import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import {BackendProvider} from "../../providers/backend/backend"
/**
 * Generated class for the AddatletaPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@Component({
  selector: 'page-addatleta',
  templateUrl: 'addatleta.html',
})
export class AddatletaPage {

  atleta: any={
    cognome: "",
    nome: "",
    societa: ""
  }

  societa: any=[];

  constructor(private viewCtrl: ViewController, private backend: BackendProvider, public navCtrl: NavController, public navParams: NavParams) {
    var questo=this;
    questo.backend.getSocieta(function(data){
      console.log("got societas",data);
      questo.societa=data.rows;
    })
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AddatletaPage');
  }

  isSaveDisabled(){
    var retvalue=false;
    if ((this.atleta.cognome.trim()=="") || (this.atleta.nome.trim()=="") || (this.atleta.societa.trim()=="")) retvalue=true;
    return retvalue;
  }

  save(){
    this.viewCtrl.dismiss(this.atleta);

  }
  cancel(){
    this.viewCtrl.dismiss()
  }
}



