import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { BackendProvider } from '../../providers/backend/backend';
import { SendpushPage } from '../../pages/sendpush/sendpush';
/**
 * Generated class for the ConnectionsPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@Component({
  selector: 'page-connections',
  templateUrl: 'connections.html',
})
export class ConnectionsPage {
  sockusers:any=[];
  loading=false;
  gcmtokens:any=[];
  view="sockets";

  constructor(public backend: BackendProvider, public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ConnectionsPage');
    this.doRefresh();
  }

  refresh(callback){
    var questo=this;
    var url=questo.backend.rooturl+"/socketusers";
    questo.backend.fetchData(url,function(data){
      console.log(data);
      questo.sockusers=data
      if (callback) callback(data);
    })
  }

  refreshgcm(callback){
    var questo=this;
    var url=questo.backend.rooturl+"/gcm/viewtokens";
    questo.backend.fetchData(url,function(data){
      console.log(data);
      questo.gcmtokens=data
      if (callback) callback(data);
    })
  }

  doRefresh(){
    var questo=this;
    questo.loading=true;
    questo.refresh(function(){
      questo.refreshgcm(function(){
        questo.loading=false;
      })
      


    })
  }

  hasGCM(s){
    var retvalue=false;
    if (s.hasOwnProperty("gcmtoken")){
      if (s.gcmtoken.trim()!="") retvalue=true;
    }
    return retvalue;

  }

  getEmail(token){
    var questo=this;
    var retvalue="";
    questo.sockusers.forEach(function(item,idx){
      if (item.gcmtoken==token) retvalue=item.email;
    })
    return retvalue;
  }

  sendPush(){
    this.navCtrl.push(SendpushPage);
  }
}



