import { Component, NgZone } from '@angular/core';
import { IonicPage, NavController, NavParams, Events } from 'ionic-angular';
import { BackendProvider } from '../../providers/backend/backend';
import {SocketService} from '../../providers/socket-service/socket-service'
import { ScorekeeperPage } from '../scorekeeper/scorekeeper';
/**
 * Generated class for the ScoreboardsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@Component({
  selector: 'page-scoreboards',
  templateUrl: 'scoreboards.html',
})
export class ScoreboardsPage {
  scoreboards:any=[];

  constructor(private zone: NgZone, private socket: SocketService, private events: Events, public backend: BackendProvider,public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidEnter() {
    var questo=this;
    console.log('ionViewDidLoad ScoreboardsPage');
    //questo.socket.sendCustomMessage("scoreboards",{});
    /*questo.events.subscribe("scoreboards",function(data){
      console.log("received scoreboards in scoreboards.ts")
      questo.scoreboards=data.scoreboards
    })*/
    questo.events.subscribe("scoreboard",function(data){
      questo.zone.run(function(){
        questo.refresh(function(){})

      })
      
    })
    questo.events.subscribe("scoreboards",function(data){
      questo.zone.run(function(){
        questo.refresh(function(){})

      })
      
    })
    questo.refresh(function(){})
  }

  refresh(callback){
    var questo=this;
    questo.backend.getScoreboards(function(data){
      questo.scoreboards=data;
      callback();
    })
  }

  ionViewDidLeave(){
    this.events.unsubscribe("scoreboard")

  }


  gotoScorekeeper(s){
    this.navCtrl.push(ScorekeeperPage,{
      mode: "slave",
      scoreboard: s
    })
  }

  hasGirone(s){
    var retvalue=false;
    if (s.hasOwnProperty("girone")){
      if (s.girone.hasOwnProperty("title")) retvalue=true;
    }
    return retvalue;
  }

}



