  getVintoText(m) {
    var text = "Non disputato";
    if (m.disputato == "yes") {
      if (m.vinto == "yes") text = "Vinto,";
      if (m.vinto == "no") text = "Perso,";
      text = text + " risultato: " + m.risultato;
    }
    return text;
  }

  getClass(m) {
    var cl = "nondisputato";
    if (m.disputato == "yes") {
      if (m.vinto == "yes") cl = "vinto";
      if (m.vinto == "no") cl = "perso";
    }
    if (m.dadisputare == "no") cl = "danondisputare";
    return cl;
  }

