import { Component, NgZone } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, Events, Platform, ViewController } from 'ionic-angular';
import { BackendProvider } from '../../providers/backend/backend';
import { SocketService } from '../../providers/socket-service/socket-service'
import * as moment from "moment";

/**
 * Generated class for the ScorekeeperPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-scorekeeper',
  templateUrl: 'scorekeeper.html',
})
export class ScorekeeperPage {
  mode = "master";
  punt1 = 0;
  punt2 = 0;
  timer: any;
  interval: any;
  timeLeft: any = 60;
  maxTime: any = 60;
  timertext = "AVVIO";
  timerstarted = false;
  timerpaused = false;
  player1: any = {
    cognome: "Atleta",
    societa: "Squadra A",
    nome: "A",

    id: "xxxx"
  }
  player2: any = {
    cognome: "Atleta",
    societa: "Squadra B",
    nome: "B",
    id: "yyyy"
  }
  socketTimerInterval = 5;
  socketTimerCount = 0;
  scoreboard: any = {
    clientid: "master",
    punt1: 0,
    punt2: 0,
    player1: {
      cognome: "Atleta",
      societa: "Squadra A",
      nome: "A",

      id: "xxxx"
    },
    player2:
    {
      cognome: "Atleta",
      societa: "Squadra B",
      nome: "B",
      id: "yyyy"
    },
    timeLeft: 60
  }
  standalonemode=false;
  sockettime=700;
  sockettimer:any;




  constructor(private viewCtrl: ViewController, private zone: NgZone, public platform: Platform, private events: Events, private socket: SocketService, private alertCtrl: AlertController, public backend: BackendProvider, public navCtrl: NavController, public navParams: NavParams) {
    var questo = this;


  }

  back() {
    this.viewCtrl.dismiss();
    //this.navCtrl.pop();
  }

  isWeb() {
    var retvalue = false;
    if (!this.platform.is("cordova")) {
      if (this.mode == "slave") retvalue = true;
    }
    return retvalue;
  }

  ionViewDidLeave() {
    var questo = this;
    if (questo.mode == "master") clearInterval(questo.interval);

    if (questo.mode == "slave") questo.events.unsubscribe("scoreboardslave");
    if (questo.mode == "master") questo.backend.removeScoreboard(questo.socket.socket.id, function () {

    });

  }

  ionViewDidEnter() {
    var questo = this;
    console.log('ionViewDidLoad ScorekeeperPage');
    if (questo.navParams.get("mode")) {
      questo.mode = questo.navParams.get("mode");
      questo.standalonemode=false;
    }
    if (questo.navParams.get("scoreboard")) {
      questo.scoreboard = questo.navParams.get("scoreboard");
     
    }
    console.log("questo.scoreboard", questo.scoreboard)
    if (String(questo.scoreboard.punt1).trim() == "") questo.scoreboard.punt1 = 0;
    if (String(questo.scoreboard.punt2).trim() == "") questo.scoreboard.punt2 = 0;
    //this.startTimer();

    console.log("questo.mode", questo.mode)
    if (questo.mode == "master") questo.timeLeft = questo.maxTime;
    questo.getScoreboards();



    /*questo.events.subscribe("scoreboards", function (data) {
      console.log("reeived scoreboards in scorekeeper.ts", data);
    })*/
    if (questo.mode == "master") questo.sendSocketScoreboard();
    if (questo.mode == "slave") {
      questo.events.subscribe("scoreboardslave", function (data) {
        console.log("reeived scoreboardslave in scorekeeper.ts", data);

        if (data) {
          questo.zone.run(function () {


            console.log("we have data!")
            if (data.hasOwnProperty("punt1")) questo.scoreboard.punt1 = data.punt1;
            //questo.punt1=data.punt1;
            if (data.hasOwnProperty("punt2")) questo.scoreboard.punt2 = data.punt2;
            if (data.hasOwnProperty("timeLeft")) questo.scoreboard.timeLeft = data.timeLeft;

          })
        }


      })
    }
  }

  getScoreboards() {
    this.socket.sendCustomMessage("getscoreboards", {})
  }

  increase(x) {
    if (this.mode != "master") return;
    this.scoreboard[x]++;
    this.sendSocketScoreboard();
  }

  decrease(x) {
    if (this.mode != "master") return;
    this.scoreboard[x]--;
    this.sendSocketScoreboard();
  }

  resetPunti() {
    var questo = this;
    questo.backend.showConfirm("Vuoi davvero resettare il punteggio ?", function (yes) {
      if (yes){
      questo.scoreboard.punt1 = 0;
      questo.scoreboard.punt2 = 0;
      questo.sendSocketScoreboard();
    }
    })

  }


  tapTimer() {
    if (!this.timerstarted) {
      this.timertext = "PAUSA";
      this.timerstarted = true;
      this.timerpaused = false;
      this.startTimer();
      return;
    }
    if (this.timerstarted) {
      this.timerpaused = !this.timerpaused;
      if (this.timerpaused) {
        this.timertext = "RIAVVIO"
      } else this.timertext = "PAUSA";
    }
  }



  startTimer() {

    var questo = this;


    questo.interval = setInterval(() => {

      if (questo.scoreboard.timeLeft > 0) {
        if (!questo.timerpaused) {
          questo.backend.playSoundDespiteAppSettings("img/tick")
          questo.scoreboard.timeLeft--;
          questo.timerstarted = true;
          questo.timertext = "PAUSA";
          questo.socketTimerCount++;
          if (questo.socketTimerCount == questo.socketTimerInterval) {
            this.sendSocketScoreboard();
            questo.socketTimerCount = 0;
          }

          if (questo.scoreboard.timeLeft == 0) {
            this.sendSocketScoreboard();
            questo.backend.playSoundDespiteAppSettings("img/endfight")
            questo.timerstarted = false;
            questo.timerpaused = false;
            questo.timertext = "AVVIO";
            clearInterval(questo.interval);


            questo.socketTimerCount = 0;
          }
        }
      } else {
        /* clearInterval(questo.timer);
         questo.backend.playSoundDespiteAppSettings("img/endfight")
         questo.timeLeft = 0;
         questo.timerstarted = false;
         questo.timerpaused = false;
         questo.timertext = "AVVIO";*/
      }
    }, 1000)
  }

  stopTimer() { //you can use this function if you want restart timer
    clearInterval(this.interval);
    this.scoreboard.timeLeft = this.maxTime;
    this.timertext = "AVVIA";
    this.timerstarted = false;
    this.timerpaused = true;
    this.sendSocketScoreboard();
    this.socketTimerCount = 0;
  }

  pauseTimer() {
    this.timerpaused = true;
    this.timertext = "RIAVVIA"
    this.sendSocketScoreboard();
  }

  resetTimer() {
    var questo = this;
    questo.backend.showConfirm("Vuoi resettare il timer ?", function (yes) {
      if (yes) {
        questo.timerpaused = true;
        questo.timerstarted = false;
        clearInterval(questo.interval);
        questo.scoreboard.timeLeft = questo.maxTime;
        questo.timertext = "AVVIO"
        questo.socketTimerCount = 0;
      }
    })
  }



  setupTimer() {
    var questo = this;
    let alert = questo.alertCtrl.create({
      title: 'Imposta il tempo in secondi',
      inputs: [
        {
          name: 'seconds',
          placeholder: 'Secondi',
          value: questo.maxTime
        }
      ],
      buttons: [
        {
          text: 'Annulla',
          role: 'cancel',
          handler: data => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'IMPOSTA',
          handler: data => {
            questo.maxTime = data.seconds;
            questo.scoreboard.timeLeft = questo.maxTime;
            questo.stopTimer();


          }
        }
      ]
    });
    alert.present();
  }

  getTime(totalseconds) {
    var t = totalseconds;
    var sec_num = parseInt(t, 10); // don't forget the second param
    var hours = Math.floor(sec_num / 3600);
    var minutes = Math.floor((sec_num - (hours * 3600)) / 60);
    var seconds = sec_num - (hours * 3600) - (minutes * 60);


    var shours = String(hours);
    var sminutes = String(minutes);
    var sseconds = String(seconds);

    if (hours < 10) { shours = "0" + shours; }
    if (minutes < 10) { sminutes = "0" + sminutes; }
    if (seconds < 10) { sseconds = "0" + sseconds; }
    return sminutes + ':' + sseconds;
  }

  getTimerClass() {
    var retvalue = 'timer';
    if (this.timerstarted) {
      if (this.timerpaused) retvalue += " colorgray blink"
    }
    return retvalue;
  }


  getTimerButtonClass() {
    var retvalue = "";
    if (this.timerstarted) {
      if (this.timerpaused) retvalue = "backgray";
      if (!this.timerpaused) retvalue = "backorange";
    }
    return retvalue;
  }

  sendSocketScoreboard() {
    var questo = this;
    questo.scoreboard.clientid = questo.socket.socket.id;
    clearTimeout(questo.sockettimer);
    questo.sockettimer=setTimeout(function(){
      questo.socket.sendCustomMessage("scoreboard", questo.scoreboard);

    },questo.sockettime);
   
    /*var data = {
      clientid: questo.socket.socket.id,
      punt1: questo.punt1,
      punt2: questo.punt2,
      timeleft: questo.timeLeft,
      player1: questo.player1,
      player2: questo.player2

    }*/
   

  }


  setResult() {
    var questo = this;

    console.log(questo.scoreboard);
    var result = questo.scoreboard.punt1 + "-" + questo.scoreboard.punt2;
    questo.backend.showConfirm("Sicuro di voler impostare il risultato sul " + result + " per questo incontro ?", function (yes) {
      if (yes) {
        var url = questo.backend.rooturl + "/matches/setresultrr/" + questo.scoreboard.garaid + "?result=" + result + "&order=" + questo.scoreboard.order;
        questo.backend.fetchData(url, function (data) {
          console.log("punteggio impostato", data);
          questo.backend.showToast("punteggio impostato su " + result);
          questo.viewCtrl.dismiss("resultchanged");
        })
      }
    })


  }



}



