import { Component } from '@angular/core';
import { NavController, NavParams, ViewController} from 'ionic-angular';

/**
 * Generated class for the AddmatchesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-addmatches',
  templateUrl: 'addmatches.html',
})
export class AddmatchesPage {

  model:any={
    matches: "",
    giornogara: "0"

  } 

  garadays=1;
  giornigara:any=[];

  constructor(public viewCtrl: ViewController, public navCtrl: NavController, public navParams: NavParams) {
      console.log("addmatches page constructor",navParams);
      this.garadays=navParams.get("giornigara");
      for (var i=0; i<this.garadays; i++){
        this.giornigara.push(String(i));
      }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AddmatchesPage');
  }


  cancel(){
    this.viewCtrl.dismiss();
  }

  save(){
    this.viewCtrl.dismiss(this.model);
  }


  isOkEnabled(){
    var retvalue=true;
    if (this.model.matches.trim()=="") retvalue=false;
    return retvalue;
  }
}



