import { Component } from '@angular/core';
import { NavController, NavParams, Events, AlertController } from 'ionic-angular';
import { BackendProvider } from '../../providers/backend/backend';
import * as moment from "moment"
/*
  Generated class for the GarePage page.

  See http://ionicframework.com/docs/v2/components/#navigation for more info on
  Ionic pages and navigation.
*/
@Component({
  selector: 'page-societa',
  templateUrl: 'societa.html'
})
export class SocietaPage {
  societa = [];

  constructor(private alertCtrl: AlertController, public events: Events, public backend: BackendProvider, public navCtrl: NavController, public navParams: NavParams) { }

  ionViewDidLoad() {

    console.log('ionViewDidLoad SocietaPage');
    this.refresh(function (data) {

    })
  }

  refresh(callback) {
    var questo = this;
    this.backend.getSocieta(function (data) {
      questo.societa = data.rows;
      console.log("societa", questo.societa);
      if (callback) callback(data);
    })
  }

  doRefresh(refresher) {


    console.log('Begin async operation', refresher);
    var questo = this;
    questo.refresh(function (data) {
      //console.log("allnews", data);

      refresher.complete();
    })



  }


  ionViewWillEnter() {
    var questo = this;
    this.events.subscribe("hwbackbutton", function (data) {
      console.log("hwbackbutton in gare.ts");
      questo.navCtrl.pop();
    })


  }

  ionViewWillLeave() {
    this.events.unsubscribe("hwbackbutton");


  }

  addSocieta() {

    var questo = this;
    const prompt = questo.alertCtrl.create({
      title: 'Societa',
      message: "Inserisci una nuova societa'",
      inputs: [
        {
          name: 'societa',
          placeholder: 'Società'
        }
      ],
      buttons: [
        {
          text: 'Cancel',
          handler: data => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'Save',
          handler: data => {
            console.log("societa", data.societa);
            console.log('Saved clicked');
            var d = new Date();
            var m = moment(d).format("YYYYMMDDHHmmss");
            var newsoc = {
              nome: data.societa,
              id: m

            }

            var url = questo.backend.rooturl + "/societa/add";
            questo.backend.postData(url, newsoc, function (pdata) {
              console.log("saved societa", pdata);
              questo.backend.showToast("Societa' " + pdata.doc.societa + " aggiunta");
              questo.refresh(function(){

              });
            })


          }
        }
      ]
    });
    prompt.present();

  }


  deleteSocieta(s){
    console.log(s)
    var questo=this;
    var nome=s.doc.nome;
    var url=questo.backend.rooturl+"/societa/delete";
    var postdata={
      id: s.doc.id
    }
    questo.backend.showConfirm("Sicuro di voler eliminare la società "+nome+" ?",function(yes){
      if (yes){
        questo.backend.postData(url,postdata,function(data){
          console.log("societa deleted",data);
          questo.refresh(function(){})
          questo.backend.showToast("Societa' "+nome+" eliminata")
        })

      }
    })
   
  }

}



