import { Component } from '@angular/core';
import { NavController, NavParams, ViewController,LoadingController } from 'ionic-angular';
import { BackendProvider } from '../../providers/backend/backend';
import * as moment from 'moment';


/**
 * Generated class for the UsereditPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@Component({
  selector: 'page-useredit',
  templateUrl: 'useredit.html',
})
export class UsereditPage {
  user: any = {
    token: "",
    email: "",
    gcmtoken: "",
    twitter: {
      oauth_token: "",
      oauth_verifier: ""
    },
    role: "BP_GUEST",
    sockid: "unknown",
    uniquedeviceid: "",
    societaid: "20160217220400",
    id: "",
    iospayment:{
      paid: false,
      expire: ""
    }
  };
  mode="edit";


  constructor(public loadingCtrl: LoadingController, public backend: BackendProvider, public viewCtrl: ViewController, public navCtrl: NavController, public navParams: NavParams) {
    var user=this.navParams.get("user");
    if (navParams.get("mode")) this.mode=navParams.get("mode");
    this.user=user.doc;
    if (!this.user.hasOwnProperty("iospayment")){
      var now=moment();
      var newmom=now.add('years', 1);
      var snewmom=newmom.format("YYYYDDMM");
      this.user.iospayment={
        paid: false,
        expire: snewmom

      }
    }
    console.log("showing user",this.user);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad UsereditPage');
  }

  cancel(){
    this.viewCtrl.dismiss();

  }

  save(){
    var questo=this;
    var url=questo.backend.rooturl+"/users/update";

    var doc=questo.user;

    if (questo.mode=="add") url=questo.backend.rooturl+"/users/add";

    console.log("preparing to save user",doc);
   

    let loading = questo.loadingCtrl.create({
      spinner: 'dots',
      content: 'Salvataggio user in corso...'
    });
  
    loading.present();
    questo.backend.postData(url,doc,function(data){
      console.log("saved user",data);
      loading.dismiss();
      questo.viewCtrl.dismiss("saved");
    })

    
  }


}



