  addPlayerToGirone(g, opts?: any) {
    var questo = this;
    var count = 0;
    questo.torneo.gironi.forEach(function (item, idx) {
      item.players.forEach(function (pitem, pidx) {
        count++;
      })

    })
    count++;

    var nome = String(count);
    var cognome = "player";
    var societa = "ASD TAEKWONDO ROZZANO";
    var uuid = UUID.UUID();
    var d = new Date();
    var m = moment(d).format("YYYYMMDDHHmmssSSS");
    var id=m+uuid;

    if (opts) {
      nome = opts.nome;
      cognome = opts.cognome;
      societa = opts.societa;
      id = opts.id;
    }
  
    
    var newplayer = {
      cognome: cognome,
      nome: nome,
      societa: societa,
      id: id,
      punti: 0,
      gf: 0,
      gs: 0
    }
    g.players.push(newplayer)
  }

