  resultChanged(ev, inc, girone) {
    var questo = this;
    var value = ev.value;
    console.log("result changed", value);

    var arr = value.split("-");
    if (arr.length != 2) return;
    if (arr.length == 2) {
      var p1 = arr[0];
      var p2 = arr[1];
      if (!questo.isNumeric(p1)) return;
      if (!questo.isNumeric(p2)) return;
      inc.punteggio1 = parseInt(p1, 10);
      inc.punteggio2 = parseInt(p2, 10);
      questo.updateClassificaGirone(girone);

    }

  }

  isNumeric(n) {
    return !isNaN(parseFloat(n)) && isFinite(n);
  }

  getIncontriGiocatiNelGirone(playerid, girone) {
    var retvalue = 0;
    var count = 0;
    girone.incontri.forEach(function (item, idx) {
      if ((item.punteggio1.trim() !== "") && (item.punteggio2.trim() != "")) {
        if (item.player1.id == playerid) count++;
        if (item.player2.id == playerid) count++;
      }
    })
    return count;

  }


